.card {
  box-shadow: none;
}

.card-stuff {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  background-color: transparent;
}

.card-icons a {
  margin: auto 10px;
}

@media screen and (min-width: 1024px) {
  .card {
    width: 50%;
    padding: 0 10px;
  }
}
